body {
  max-width: 100%;
  margin: 0;
  padding: 0;
}

img {
  width: 100%;
  height: 100%;
  display: block;
}

.wrapper-select {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 20px;
  display: flex;
}

.cat-info {
  width: 80vw;
  padding: 0 30px 30px;
}

.error {
  width: 100%;
}

.is-hidden {
  display: none;
}

.loader {
  letter-spacing: 2px;
  color: #000;
  text-shadow: 0 0 1px #fff;
  margin-top: 40px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 48px;
  font-weight: bold;
  animation: smokeOut 1s ease-in-out infinite alternate;
}

.loader:before {
  content: "Loading data, please wait...";
}

@keyframes smokeOut {
  100% {
    opacity: .08;
    filter: blur(5px);
    letter-spacing: 4px;
  }
}

